html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button {
  background: none;
  border: 0; }

a {
  color: #047AE6;
  text-decoration: none; }

* {
  box-sizing: border-box; }

.text {
  font-family: "Open Sans", "Helvetica Neue", Helvetica, sans-serif; }

.title {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 600; }

.is-title {
  font-size: 48px;
  line-height: 64px; }

.is-subheader {
  font-size: 32px;
  line-height: 40px; }

.is-body {
  font-size: 20px;
  line-height: 32px; }

.is-medium {
  font-size: 18px;
  line-height: 24px; }

.is-caption {
  font-size: 16px;
  line-height: 24px; }

.is-small {
  font-size: 12px;
  line-height: 16px; }

.is-xsmall {
  font-size: 10px;
  line-height: 14px; }

.is-regular {
  font-weight: 400; }

.is-semibold {
  font-weight: 600; }

.is-bold {
  font-weight: 700; }

.is-underline {
  text-decoration: underline; }

.is-italic {
  font-style: italic; }

.letter-spacing {
  letter-spacing: 1px; }

code {
  margin: 0 0.25em;
  padding: 0 0.125em;
  border: 1px solid #C2CDD6;
  border-radius: 4px; }

.is-txtPrimary {
  color: #047AE6; }

.is-txtWhite {
  color: #FFF; }

.is-txtGrey {
  color: #2E3C43; }

.is-txtSoftGrey {
  color: #6F757B; }

.is-txtAlert {
  color: #E20703; }

.is-txtSoftBlue {
  color: #F2F6F9; }

.is-txtDarkBlue {
  color: #003267; }

.is-bgWhite {
  background-color: #FFF; }

.is-bgSoftBlue {
  background-color: #F2F6F9; }

.is-bgAlert {
  background-color: #E20703; }

@media (max-width: 800px) {
  .u-hideTablet {
    display: none; } }

@media (max-width: 600px) {
  .u-hideMobile {
    display: none; } }

.u-showMobile {
  display: none; }

@media (max-width: 600px) {
  .u-showMobile {
    display: flex; } }

.u-no-decoration {
  text-decoration: none; }
  .u-no-decoration:hover {
    text-decoration: none; }

.u-is-disabled {
  opacity: 0.5;
  pointer-events: none; }

.u-overflow-hidden {
  overflow: hidden; }

.u-width--100 {
  width: 100%; }

.u-inline {
  display: inline; }

.u-list__style--decimal {
  list-style-type: decimal; }

.u-list__position--inside {
  list-style-position: inside; }

.navbar-placeholder {
  display: flex;
  position: fixed;
  z-index: 11;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 64px;
  border-bottom: 1px solid #FFF;
  background-color: #047AE6; }

.navbar-placeholder-logo {
  width: 24px;
  height: 24px; }

#header {
  padding-top: 64px; }
  #header.has-user-notification {
    padding-top: 124px; }
